<template>
  <div>
    <navCite/>
    <div class="container bg-light">
      <h1 class="p-5 text-dark text-center">
        <b>Listado de Citas</b>
      </h1>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        id="table-cite"
        show-empty
        hover
        small
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="cites"
        :fields="columns"
        :filter="filter"
        :curren-page="currentPage"
        :per-page="10"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        @filtered="onFiltered"
      >
        <template v-slot:cell(paciente)="data">{{data.value.nombre}} {{data.value.apellido}}</template>
        
        <template v-slot:cell(medico)="data">{{data.value.nombre}} {{data.value.apellido}} - {{data.value.especialidad.descripcion}}</template>
        
        <template v-slot:cell(fechaCita)="data">{{convertDateToString(data.item.fechaCita)}}</template>
        
        <template v-slot:cell(estadoCita)="data">
          <span
            v-if="data.item.estadoCita.descripcion==='En espera'"
            class="btn btn-warning"
          >{{data.item.estadoCita.descripcion}}</span>
          <span v-else class="btn btn-danger">{{data.item.estadoCita.descripcion}}</span>
        </template>
      </b-table>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-pagination
            size="md"
            :total-rows="totalRows"
            v-model="currentPage"
            :per-page="10"
            @input="find(currentPage)"
          ></b-pagination>
        </b-col>
      </b-row>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL } from "@/variables.js";
import axios from "axios";
import navCite from "@/components/cite/navCite.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "citas",
  data() {
    return {
      columns: [
        {
          key: "medico",
          label: "Médico - Especialidad",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "paciente",
          label: "Paciente",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "motivo"
        },
        {
          key: "fechaCita",
          label: "Fecha de Espera"
        },
        {
          key: "estadoCita",
          label: "Estado"
        }
      ],
      cites: [],
      cite:[],
      currentPage:1,
      totalRows:null,
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null
    };
  },
  methods: {
    async findAll() {
      axios
        .get(`${URL}/api/citas`)
        .then(response => (this.cites = response.data))
        .catch(error => console.log(error));
    },
    find(currentPage){
      axios
      .get(`${URL}/api/cita?page=${currentPage-1}`)
      .then(response=>{
        this.cite=response.data.content
        this.totalRows=response.data.totalElements
      })
    },
    convertDateToString(date) {
      let dateObject = new Date(date);
      let formatter = new Intl.DateTimeFormat("es", {
        year: "numeric",
        month: "long",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
        second: "numeric"
      });
      return formatter.format(dateObject);
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  mounted(currentPage){
    this.find(currentPage)
    this.findAll()
  },
  components: {
    navCite,
    Footer
  }
};
</script>

